import * as React from "react";
import save from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import { useFetcher } from 'shared/components/ajax';
import Navbar from "shared/components/ui/Navbar";
import { Button, Grid, Typography, withStyles } from "@material-ui/core";
import DesaIcon from '@material-ui/icons/HomeWork';
import StoreIcon from '@material-ui/icons/Store';
import DriverIcon from '@material-ui/icons/DriveEta';
import { navigate } from "gatsby";

/**
 * @typedef {import('../../../../shared/types/userInfo').UserInfo} UserInfo
 */

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    container: {
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        '& .MuiGrid-container': {
            margin: 'auto 0',
        },
        '@media (min-width: 960px)': {
            '&  .MuiButton-root': {
                height: 300
            }
        }
    },
    button: {
        margin: 20,
        width: 'calc(100% - 40px)',
        '& .MuiButton-label': {
            flexDirection: 'column',
            display: 'flex',
        },
        '& .MuiSvgIcon-root': {
            marginBottom: theme.spacing(1),
            objectFit: 'contain',
            width: '40px',
            height: '40px',
        }
    },
})

const Page = withStyles(styles)(({ classes }) => {
    /**
     * @type {UserInfo}
     */
    const data = useFetcher('/auth/info', (d) => {
        save.userInfo = d;
    });
    return (
        <Defer data={data}>
            {() => (
                <div className={classes.container}>
                    <Navbar />
                    <Grid container>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h4" gutterBottom>
                                OJOLALI DASAWARSALINK
                            </Typography>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/desa/')}>
                                <DesaIcon />
                                <Typography gutterBottom>
                                    Agen Desa
                                </Typography>
                            </Button>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/driver/')}>
                                <DriverIcon />
                                Agen Driver
                            </Button>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Button variant="outlined" className={classes.button} onClick={() => navigate('/store/')}>
                                <StoreIcon />
                                Agen Toko
                            </Button>
                        </Grid>
                    </Grid>
                </div>
            )}
        </Defer>
    );
})

export default Page;
